export default {
	name: 'ZPopup',
	props: {
		visible: {
			type: Boolean,
			default: false
		},
		maskClick: {
			type: Boolean,
			default: true
		}
	},
	data() {
		 return {
			
		 }
	},
	methods: {
		hidePopup() {
			if(!this.maskClick) return
			
			this.$emit('change', {
				show: false
			})
		}
	}
}
